import React, { useEffect, useState, PureComponent } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import getKey from '../../utils/KeyGenerator';
import { BorderBottomRounded, FlashOnTwoTone } from '@material-ui/icons';
import shadows from '@material-ui/core/styles/shadows';

const TideGraph = ({
    tideNow
}) => {

    const [isDataConverted, setIsDataConverted] = useState(false);
    const [convertedTideData, setConvertedData] = useState();
    useEffect(() => {
        console.log(`tideNow: ${JSON.stringify(tideNow.predictions, null, 2)}`)
        // const newData = tideNow.data;
        const newData = tideNow.predictions;
        newData.map((tide) => {
            tide.name = (tide.type === 'H') ? 'HIGH' : 'LOW';
            tide.pv = tide.v;
            tide.amt = tide.s;
        })

        //console.log(`newData: ${JSON.stringify(newData,null,2)}`)
        setConvertedData(newData);
        setIsDataConverted(true);
    }, []);
    useEffect(() => {
        // console.log(`convertedTideData: ${(isDataConverted)?JSON.stringify(convertedTideData,null,2):'not loaded'}`);
    }, [isDataConverted]);
    const box = {
        backgroundColor: 'black',
        fontSize: 20,
    }
    const getNextLowTideIndex = () => {
        const predictions = convertedTideData;
        const currentTime = new Date();

        let closestIndex = -1;
        let closestTimeDiff = Infinity;

        for (let i = 0; i < predictions.length; i++) {
            const predictionTime = new Date(predictions[i].t);
            const predictionDate = Number(String(predictionTime.getDate()).padStart(2, '0'));
            const currentDate = Number(String(currentTime.getDate()).padStart(2, '0'));
            const isAfter = (predictionTime > currentTime) ? true : false;

            console.log(`${i}  - type: ${predictions[i].type}`)
            console.log(`${i}  - currentTime: ${currentTime}`)
            console.log(`${i}  - predictionTime: ${predictionTime}`)
            console.log(`${i}  - after: ${isAfter}`)
            console.log(`${i}  - currentTime: ${String(currentTime.getDate()).padStart(2, '0')}`)
            console.log(`${i}  - predictionTime: ${String(predictionTime.getDate()).padStart(2, '0')}`)

            if (predictions[i].type == 'L' && isAfter) {
                closestIndex = i;
                console.log(`# closestIndex: ${closestIndex}`)
                return closestIndex;
            } else if ((predictionDate > currentDate)) {
                console.log(`${i}  - type: ${predictions[i].type}`);
                console.log(`${i}  - currentTime: ${currentTime}`)
                console.log(`${i}  - currentTime: ${String(currentTime.getDate()).padStart(2, '0')}`)
                console.log(`${i}  - predictionTime: ${predictionTime}`)
                console.log(`${i}  - predictionTime: ${String(predictionTime.getDate()).padStart(2, '0')}`)
                closestIndex = i;
                const timeDiff = predictionTime - currentTime;
                if (timeDiff < closestTimeDiff) {
                    closestIndex = i;
                    closestTimeDiff = timeDiff;
                    console.log(`closestIndex: ${closestIndex}`)
                    return closestIndex;
                }
                console.log(`closestIndex: ${closestIndex}`)
                return closestIndex;
            }
        }
        console.log(`closestIndex: ${closestIndex}`)
        return closestIndex;
    };

    const getNextTideIndex = () => {
        const predictions = convertedTideData;
        const currentTime = new Date();

        let closestIndex = -1;
        let closestTimeDiff = Infinity;

        for (let i = 0; i < predictions.length; i++) {
            const predictionTime = new Date(predictions[i].t);
            const predictionDate = Number(String(predictionTime.getDate()).padStart(2, '0'));
            const currentDate = Number(String(currentTime.getDate()).padStart(2, '0'));
            const isAfter = (predictionTime > currentTime) ? true : false;

            console.log(`${i}  - type: ${predictions[i].type}`)
            console.log(`${i}  - currentTime: ${currentTime}`)
            console.log(`${i}  - predictionTime: ${predictionTime}`)
            console.log(`${i}  - after: ${isAfter}`)
            console.log(`${i}  - currentTime: ${String(currentTime.getDate()).padStart(2, '0')}`)
            console.log(`${i}  - predictionTime: ${String(predictionTime.getDate()).padStart(2, '0')}`)

            if ((predictionTime > currentTime)) {
                console.log(`${i}  - type: ${predictions[i].type}`);
                console.log(`${i}  - currentTime: ${currentTime}`)
                console.log(`${i}  - currentTime: ${String(currentTime.getDate()).padStart(2, '0')}`)
                console.log(`${i}  - predictionTime: ${predictionTime}`)
                console.log(`${i}  - predictionTime: ${String(predictionTime.getDate()).padStart(2, '0')}`)
                closestIndex = i;
                const timeDiff = predictionTime - currentTime;
                if (timeDiff < closestTimeDiff) {
                    closestIndex = i;
                    closestTimeDiff = timeDiff;
                    console.log(`closest tide: (1) ${closestIndex}`)
                    return closestIndex;
                }
                console.log(`closest tide: (2) ${closestIndex}`)
                return closestIndex;
            }
        }
        console.log(`closest tide: (3) ${closestIndex}`)
        return closestIndex;
    };





    class CustomizedLabel extends PureComponent {
        render() {
            const { x, y, stroke, value } = this.props;

            return (
                <text x={x-15} y={y-10} dy={-4} fill={'white'} fontWeight='bold' fontSize={15} rotate={5} textAnchor="middle">
                    {value}ft
                </text>
            );
        }
    }

    class CustomizedAxisTick extends PureComponent {
        render() {
            const { x, y, stroke, payload } = this.props;

            return (
                <g transform={`translate(${x},${y})`}>
                    <text x={0} y={-12} dy={16} textAnchor="end" fill="#666" transform="rotate(0)">
                        {payload.value}ft
                    </text>
                </g>
            );
        }
    }
    class CustomizedXAxisTick extends PureComponent {
        render() {
            const { x, y, stroke, payload } = this.props;

            return (
                <g transform={`translate(${x},${y})`}>
                    <text x={24} y={0} dy={16} textAnchor="end" backgroundColor='#0099ff' fill="#666" fontSize="15" transform="rotate(0)">
                        {payload.value}
                    </text>
                </g>
            );
        }
    }

    return (
        (isDataConverted)
            ? <div>
                <div className='ml--30 pr-20'>
                    <ResponsiveContainer width='100%' height={300}>
                        <LineChart width='100%' height={50} data={convertedTideData}>
                            <CartesianGrid />
                            <XAxis dataKey="time" tick={<CustomizedXAxisTick />}/>
                            <YAxis dataKey="tide" tick={<CustomizedAxisTick />}/>
                            <Line type="monotone" dataKey="tide" stroke="#0099ff" activeDot={{ r: 8 }} strokeWidth={5} dot={false} /*label={<CustomizedLabel />}*//>
                            <Tooltip contentStyle={box} />
                            <Legend />
                        </LineChart>
                    </ResponsiveContainer>
                </div>
                <div>
                    {
                        convertedTideData.map((prediction, index) => {
                            const predictions = convertedTideData;
                            //const nextLowTideIndex = getNextLowTideIndex();
                            const nextTideIndex = getNextTideIndex();
                            const currentTime = new Date();
                            const predictionTime = new Date(predictions[index].t);
                            const getTide = (tide) => (tide === 'H' || tide === 'HIGH') ? 'HIGH' : 'LOW';
                            return <div>
                                {
                                    (/*Number(String(currentTime.getDate()).padStart(2, '0')) > Number(String(predictionTime.getDate()).padStart(2, '0')) &&*/ (getNextTideIndex() === (index)))
                                        ? <div key={getKey('prediction')} className='containerBox flexContainer bg-oceanblue color-dark bold'>
                                            <div className='flex3Column'>{Number(prediction.tide).toFixed(1)}'</div>
                                            <div className='flex3Column'>{getTide(prediction.name)}</div>
                                            <div className='flex3Column'>{prediction.time}</div>
                                        </div>
                                        : <div key={getKey('prediction')} className='containerBox flexContainer'>
                                            <div className='flex3Column'>{Number(prediction.tide).toFixed(1)}'</div>
                                            <div className='flex3Column'>{getTide(prediction.name)}</div>
                                            <div className='flex3Column'>{prediction.time}</div>
                                        </div>
                                }
                            </div>
                        })
                    }
                </div>
            </div>
            : null
    )
};

export default TideGraph;