import React, { useCallback, useState, useEffect } from 'react';
import { PieChart, Pie, Cell, Tooltip } from 'recharts';
import useCurrentTime from '../../utils/useCurrentTime';
import TideGraph from './TideGraph';
import validate from '../../utils/validate';

const TideChart = () => {

    const [tideNow, setTideNow] = useState(null);
    const [graphData, setGraphData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [loaded, setLoaded] = useState(false);

    const [status, setStatus] = useState({
        tide: '',
        tideDirection: localStorage.getItem('tideDirection') || '?',
        height: null,
        updated: false
    });
    const [errorMessage, setErrorMessage] = useState('');

    const time = useCurrentTime();
    const startTime = time[0].startTime;
    const endTime = time[0].endTime;
    const tideNowLink = `https://api.tidesandcurrents.noaa.gov/api/prod/datagetter?begin_date=${startTime}&end_date=${endTime}&station=9410660&product=water_level&datum=mllw&units=english&time_zone=lst_ldt&application=web_services&format=json`;
    const significantTides = `https://api.tidesandcurrents.noaa.gov/api/prod/datagetter?product=predictions&application=NOS.COOPS.TAC.WL&begin_date=${startTime}&end_date=${endTime}&datum=MLLW&station=9410230&time_zone=lst_ldt&units=english&interval=hilo&format=json`;

    useEffect(() => {

        const fetchData = async () => {
            try {
                const cachedData = JSON.parse(localStorage.getItem('tideNow'));
                const halfHourAgo = new Date().getTime() - .5 * 60 * 60 * 1000;
                
                    //console.log(`NewTide => tideNowLink: ${tideNowLink}`)
                    //const response = await fetch(tideNowLink);
                    console.log(`NewTide => significantTides: ${significantTides}`)
                    const response = await fetch(significantTides);
                    if (!response.ok) {
                        throw new Error('Failed to fetch data');
                    }
                    const data = await response.json();
                    //setTideNow(data.data);
                    setTideNow(data);
                    localStorage.setItem('tideNow', JSON.stringify({ data, timestamp: new Date().getTime() }));
                    setLoading(false);
                    setErrorMessage('');
                
            } catch (error) {
                setTideNow(null);
                setLoading(false);
                setErrorMessage('Error fetching data. Please try again later.');
            }
        };
        fetchData();
    }, []);

    const convertTo12HourTime = (militaryTime) => {
        let [hours, minutes] = militaryTime.split(':').map(Number);
        let period = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12 || 12; // Converts '0' or '12' to '12'
        return `${hours}:${minutes < 10 ? '0' + minutes : minutes} ${period}`;
    }
    
    useEffect(() => {
        if (validate(tideNow) !== null) {
            //console.log(`TideChart => tideNow: ${JSON.stringify(tideNow,null,2)}`)
            //const newGraphData = [...tideNow.data];
            const newGraphData = [...tideNow.predictions];
            newGraphData.map((prediction) => {
                prediction.name = prediction.type;
                prediction.uv = prediction.v;
                prediction.pv = prediction.v;
                prediction.tide = Number(prediction.v).toFixed(1);
                prediction.Tide = Number(prediction.v).toFixed(1);
                prediction.Tide_Height = Number(prediction.v).toFixed(1);
                prediction.feet = Number(prediction.v).toFixed(1);
                prediction.Feet = Number(prediction.v).toFixed(1);
                prediction.amt = prediction.v;
                prediction.ht = String(Number(prediction.v).toFixed(1)+'ft');
                prediction.time = convertTo12HourTime(prediction.t.split(' ')[1]).replace(' ','');
            })
            setGraphData(newGraphData);
        }
    }, [tideNow]);
    useEffect(() => {
        console.log(`graphData: ${JSON.stringify(graphData,null,2)}`)
        setLoaded(true);
    }, [graphData]);
    return (
        <div className='containerBox'>
            {
                (tideNow && loaded)
                ?  <TideGraph tideNow={tideNow} />
            /*
                ? tideNow.data.map((perdiction)=> <div className='flexContainer'>
                        <div className='flex3Column'>
                            {perdiction.t}
                        </div>
                        <div className='flex3Column'>
                           V: {perdiction.v}'
                        </div>
                        <div className='flex3Column'>
                            {perdiction.s}/hr
                        </div>
                    </div>
                )
            */
                : null
            }
           
        </div>
    );
}

export default TideChart;